import { setStgAskForGPTText } from '../common/storage';
import {
  aliLog,
  setInstallTime,
  getInstallTime,
  getClientVersion,
  getUUID,
} from '../common/ali_log';
import C from '../common/constant';
import { updateRemoteConfigEventHandler } from '../utils/remoteConfig';

chrome.runtime.onInstalled.addListener(async (details) => {
  if (details.reason === 'install') {
    //
    await setInstallTime();

    //
    chrome.tabs.create({ url: 'https://linkedradar.com/free-linkedin-tools?utm_source=gpt-ext' });
    chrome.tabs.create({ url: 'https://www.zhihu.com/' }, function () {});

    await aliLog(`${C.LOG_TYPE}0010`);
  }
});

//  content-scripts 
chrome.runtime.onMessage.addListener(async (request) => {
  if (request.type === 'create') {
    const tabs = await chrome.tabs.query({ active: true, currentWindow: true });
    await chrome.storage.local.set({ appTabId: tabs[0]?.id });

    // 
    await setStgAskForGPTText(request.text);

    console.log('ask_for_chatgpt_text ', request.text);

    chrome.tabs.create({ url: 'https://chat.openai.com/chat' });
  } else if (request.type === C.MESSAGE_FROM_CHATGPT_COPY) {

    const tabs = await chrome.tabs.query({ active: true, currentWindow: true });
    const storage = await chrome.storage.local.get('appTabId');
    const appTabId = storage.appTabId;

    if (appTabId) {
      //  mainview  chatgpt 
      await chrome.tabs.sendMessage(appTabId, {
        type: C.MESSAGE_CHATGPT_MSG,
        text: request.text,
      });

      // tab:  instagram ;  chatgpt 
      await chrome.tabs.update(appTabId, { active: true });
      await chrome.tabs.remove([tabs[0].id]);
    } else {

      console.log('tab id ');

    }
  } else if (request.type === C.MESSAGE_SYNC_CONFIG) {
    updateRemoteConfigEventHandler();
  } else if (request.code === 'alilog_send') {
    //
    aliLog(request.event_type, request.zhihu_profile, request.event_data);
  }
});

// ctrl/command + M 
chrome.commands.onCommand.addListener(async (command) => {
  console.log(`Command: ${command}`);
  if (command === 'toggle_ChatGPT') {
    console.log('mainView');
    const tabs = await chrome.tabs.query({ active: true, currentWindow: true });
    if (tabs && tabs.length > 0) {
      //  zhihuApp ,  mainView
      chrome.tabs.sendMessage(tabs[0].id, {
        type: C.MESSAGE_SHORTCUTS,
        text: 'mainView',
      });
    }
  }
});



chrome.action.onClicked.addListener(async function (tab) {
  if (tab.url.indexOf('www.zhihu.com') === -1) {
    await chrome.tabs.create({ url: 'https://www.zhihu.com/' });
  }
});

//
setTimeout(async function () {
  let g_uuid = await getUUID();
  let install_time = await getInstallTime();
  let client_version = await getClientVersion();
  const event_time = Math.floor(Date.now() / 1000);
  chrome.runtime.setUninstallURL(
    `https://linkedradar.com/feedback-form/chatgpt-zhihu-feedback?version=${client_version}&uuid=${g_uuid}&install_time=${install_time}&event_time=${event_time}&event_source=2c&event_type=2c0300`
  );
}, 500);

